.html
{
    height: 100%;
}

.footer{
    background-color: #222;
    color: #fff;
    padding: 1em;
    text-align: center;
    bottom: 0;
    width: 100%;
    display: flex;

    min-height: 100%;
    flex-direction: column;
}

.social_list{
    display: flex;
    justify-content: center;
    list-style-type: none;
}

.social_list li{
    margin: 0 1em;
}

.social_list li:hover{
    color: #fda900;
}

.social_list svg{
    font-size: 1.5em;
    cursor: pointer;
}

.copy_right{
    margin-top: 1em;
}

.copy_right span{
    font-weight: bold;
    color: #ffbb33;
}